import React, { useEffect, useRef, useState } from 'react';
import Chart from 'chart.js/auto';

export const HistogramChart = ({data, labels, backgroundColor, borderColor, borderWidth}) => {
  const chartRef = useRef(null)
  const [chart, setChart] = useState(null)

  useEffect(() => {
    renderChart()

    return () => {
      destroyChart()
    }
  }, [data, labels, backgroundColor, borderColor, borderWidth])

  function renderChart() {
    // Destroy the existing chart instance, if any
    destroyChart();

    // Create a new Chart instance with the updated data and options
    const chart = new Chart(chartRef.current, {
      type: 'bar',
      data: {
        labels: labels,
        datasets: [{
          label: 'Roll Frequency',
          data: data,
          backgroundColor: backgroundColor,
          borderColor: borderColor,
          borderWidth: borderWidth
        }]
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          x: {
            beginAtZero: true
          },
          y: {
            beginAtZero: true
          }
        }
      }
    });
    setChart(chart)
  }

  function destroyChart() {
    // Destroy the Chart instance, if exists
    if (chart) {
      chart.destroy();
      setChart(null);
    }
  }

  return (
    <div>
      <canvas ref={chartRef} />
    </div>
  );
}

export default HistogramChart;
